import React from "react";

const Resume = ({ classicHeader, darkTheme }) => {
  const educationDetails = [
    {
      yearRange: "2016-2020",
      title: "BA in Creative Digital Media",
      place: "Technological University Dublin",
      desc: "Developed a comprehensive and practical understanding of digital media, with a strong emphasis on innovative videography, effective digital marketing strategies, and engaging multimedia storytelling techniques.",
    },
    {
      yearRange: "2014-2016",
      title: "Secondary Education",
      place: "Dublin High School",
      desc: "Focused on developing foundational skills in digital arts and technology, laying the groundwork for a career in digital media and videography.",
    },
  ];
  
  const experienceDetails = [
    {
      yearRange: "Nov 2022 - Nov 2023",
      title: "Media Manager",
      place: "All Nations Church Dublin",
      desc: "Coordinated and trained volunteers in recording and live-streaming church services, and managed promotional material creation.",
    },
    {
      yearRange: "Jun 2022 - Sep 2022",
      title: "Audio-Video Editor",
      place: "Alfa Omega TV, Timisoara, Romania",
      desc: "Collaborated with creative teams on video production, ensuring technical and creative standards were met.",
    },
    {
      yearRange: "Jun 2020 - Feb 2022",
      title: "Video Team Coordinator",
      place: "All Nations Church Dublin",
      desc: "Led a team of camera operators for live-streaming and recording services, and produced digital promotional content.",
    },
    {
      yearRange: "Sep 2014 - Sep 2016",
      title: "Freelance Photographer",
      place: "Various Businesses",
      desc: "Specialized in freelance photography, including studio setup, client engagement, and post-production. Delivered high-quality product photography and social media content to enhance client brand presence."
    },    
  ];
  

  const skills = [
    {
      name: "Design Principles",
      percent: 95,
    },
    {
      name: "Adobe Premier Pro",
      percent: 90,
    },
    {
      name: "Narrative Development and Storytelling",
      percent: 90,
    },
    {
      name: "Adobe Photoshop",
      percent: 85,
    },
    {
      name: "Multimedia Presentation",
      percent: 100,
    },
    {
      name: "Adobe Illustrator",
      percent: 90,
    },
  ];

  return (
    <section
      id="resume"
      className={"section " + (darkTheme ? "bg-dark-1" : "")}
    >
      <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              "text-24  text-uppercase fw-600 w-100 mb-0 " +
              (darkTheme ? "text-muted opacity-1" : "text-light opacity-4")
            }
          >
            Summary
          </h2>
          <p
            className={
              "text-9 text-dark fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
              (darkTheme ? "text-white" : "text-dark")
            }
          >
            {" "}
            Resume
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        <div className="row gx-5">
          {/* My Education */}
          <div className="col-md-6">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Education
            </h2>
            {educationDetails.length > 0 &&
              educationDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
          {/* My Experience */}
          <div className="col-md-6">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Experience
            </h2>
            {experienceDetails.length > 0 &&
              experienceDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
        </div>
        {/* My Skills */}
        <h2
          className={
            "text-6 fw-600 mt-4 mb-4 " + (darkTheme ? "text-white" : "")
          }
        >
          My Skills
        </h2>
        <div className="row gx-5">
          {skills.length > 0 &&
            skills.map((skill, index) => (
              <div key={index} className="col-md-6">
                <p
                  className={
                    " fw-500 text-start mb-2 " +
                    (darkTheme ? "text-light" : "text-dark")
                  }
                >
                  {skill.name}{" "}
                  <span className="float-end">{skill.percent}%</span>
                </p>
                <div
                  className={
                    "progress progress-sm mb-4 " + (darkTheme ? "bg-dark" : "")
                  }
                >
                  <div
                    className="progress-bar"
                    role="progressbar"
                    style={{ width: skill.percent + "%" }}
                    aria-valuenow={skill.percent}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
              </div>
            ))}
        </div>
        <div className="text-center mt-5">
          <a
            className="btn btn-outline-secondary rounded-pill shadow-none"
            href="CV.pdf"
            target="_blank"
          >
            Download CV
            <span className="ms-1">
              <i className="fas fa-download" />
            </span>
          </a>
        </div>
      </div>
    </section>
  );
};

export default Resume;
